.menu {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 70px;
    left: 0;
    transform: translateX(-1300%);
    transition: all 0.5s;
}

.menu.active{
    transform: translateX(0);
}

.blur {
    width: 100vw;
    height: 100vh;
    left: 350px;
    backdrop-filter: blur(4px);
    position:absolute;
}

.menu__content {
    width: 350px;
    height: 100%;
    background-color: rgb(121, 155, 250);
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: all 0.5s;
}

.menu__header{
    padding: 10px;
    font-size: 4rem;
    color: rgb(236, 235, 235);
    border-bottom: 2px solid rgb(223, 223, 223);
    width: 100%;
    text-align: center;
}

ul li a{
    color: rgb(255, 255, 255);
    text-decoration: none;
    padding: 30px;
    font-size: 2.7rem;
    transition: 0.3s;
    display: inline-block;
    position: relative;
}

ul li a:hover {
    color: rgb(253, 184, 184);
    transform: translateY(-20px);
}

ul{
    text-align: center;
    flex-direction: column;
    display: flex;
    align-items: center;
}

ul li {
    margin: 10px;
}

@media (max-width: 950px) {
    .menu__content {
        width: 100%;
    }

    .blur {
        backdrop-filter: none;
    }
    .menu__header{
        font-size: 5rem;}
    ul li a{
        font-size: 4.4rem;
     }
}
